/* eslint-disable no-nested-ternary */
/* eslint-disable no-bitwise */
import React, { memo } from "react";
import moment from "moment";
import countdown from "../../../utils/countdown/countdown";
import { Typography } from "@material-ui/core";
import { useForceUpdate } from "../../../utils/ReactUtils";

function getRemainingTime(time) {
  const timeString = moment(time)
    .countdown(
      Date.now(),
      countdown.DAYS | countdown.HOURS | countdown.MINUTES | countdown.SECONDS,
      1
    )
    .toString();
  return moment(Date.now()).isBefore(time)
    ? `${timeString} remaining.`
    : "Expired.";
}

export default memo(({ time, text }) => {
  const [timer, setTimer] = React.useState();
  const forceUpdate = useForceUpdate();
  React.useEffect(() => {
    clearInterval(timer);
    setTimer(
      setInterval(() => {
        forceUpdate();
      }, 1000)
    );
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <Typography display="inline" style={{ color: "#ff782c" }}>
      {text || (time ? `${getRemainingTime(time)}` : "...")}
    </Typography>
  );
});
