function subRoute(endpoint, ...sub) {
  const [path, query] = (endpoint && endpoint.split("?")) || [];
  const [, hash] = (query && query.split("#")) || [];
  return `${path || ""}${sub ? `/${sub.join("/")}` : ""}${
    query ? `?${query}` : ""
  }${hash ? `#${hash}` : ""}`;
}

const WebViewServer = `${window.location.protocol}//${window.location.host}`;

const KatalonContent = subRoute(WebViewServer, "content");
const KatalonInfo = subRoute(WebViewServer, "info");
const KatalonTracking = subRoute(WebViewServer, "tracking");
const KatalonAction = subRoute(WebViewServer, "action");
const HTMLEmailTemplate = subRoute(WebViewServer, "template");

const PricingPage = "https://www.katalon.com/pricing";
const SignupPage = "https://www.katalon.com/sign-up";
const HelpCenter = "https://www.katalon.com/help-center";
const Store = "https://store.katalon.com";
const Tutorials = "https://docs.katalon.com/katalon-studio/docs/overview.html";
const ForumPage = "https://forum.katalon.com";
const Academy =
  "https://academy.katalon.com/?utm_source=ks_app&utm_medium=header_icon&utm_campaign=inapp_promote";

const KatalonEndpoints = {
  WebViewServer,
  KatalonContent,
  KatalonInfo,
  KatalonTracking,
  KatalonAction,
  PricingPage,
  SignupPage,
  HelpCenter,
  Store,
  Tutorials,
  Academy,
  ForumPage,
  HTMLEmailTemplate,
};

export default KatalonEndpoints;
