import KatalonEndpoints from '../variables/KatalonEndpoints';
import KatalonInfo from '../variables/KatalonInfo';
import axios from 'axios';

export default class KSService {

  static async fetchContent(url) {
    let data = await axios.get(`${KatalonEndpoints.KatalonContent}?url=${encodeURIComponent(url)}`);
    return data.data;
  }

  static async fetchInfo(info) {
    const randomInt = Math.round(Math.random() * 100000000000000);
    let data = await axios.get(`${KatalonEndpoints.KatalonInfo}?t=${randomInt}&info=${info}`);
    return data.data;
  }

  static async fetchTrialStatus() {
    return await this.fetchInfo(KatalonInfo.TrialStatus);
  }

  static async fetchVersion() {
    return await this.fetchInfo(KatalonInfo.Version);
  }

  static async fetchLicenseType() {
    return await this.fetchInfo(KatalonInfo.LicenseType);
  }

  static async fetchBuildNumber() {
    return await this.fetchInfo(KatalonInfo.BuildNumber);
  }

  static async fetchUserProfile() {
    return await this.fetchInfo(KatalonInfo.UserProfile);
  }

  static async fetchExpirationDate() {
    return await this.fetchInfo(KatalonInfo.ExpirationDate);
  }
}
