/* eslint-disable no-nested-ternary */
import Button from '@material-ui/core/Button';
import Link from '@material-ui/core/Link';
import React from 'react';
import KSTrackingService from '../../../services/KSTrackingService';

export default function StartPageLink({
  children, className, onClick, href, area, asLink = true, asButton = false, color = 'primary', style = {}, followLink = true,
  underline="none"
}) {
  function handleClick(event) {
    KSTrackingService.trackStartPage(area, href);
    if (onClick) {
      onClick(event);
    }
    if (followLink && href) {
      window.location.href = href;
      event.preventDefault();
      event.stopImmediatePropagation();
    }
  }
  return (
    asButton ? (
      <Button href={href} color={color} className={className} style={style} onClick={handleClick}>
        {children}
      </Button>
    ) : (
      asLink ? (
        <Link
          component="button"
          variant="subtitle1"
          color={color}
          href={href}
          className={className}
          onClick={handleClick}
          style={style}
          underline={underline}
        >
          {children}
        </Link>
      ) : (
        <a href={href} className={className} style={style} onClick={handleClick}>
          {children}
        </a>
      )
    )
  );
}
