import axios from 'axios';
import KatalonEndpoints from '../variables/KatalonEndpoints';
import KatalonTrackingEvent from '../variables/KatalonTrackingEvent';

export default class KSTrackingService {
  static async track(eventName, event) {
    const eventObject = { event: eventName, ...event };
    return axios.post(`${KatalonEndpoints.KatalonTracking}?event=${encodeURIComponent(JSON.stringify(eventObject))}`, {})
  }

  static async trackStartPage(area, url) {
    const event = { area, url };
    return this.track(KatalonTrackingEvent.StartPage, event);
  }
}
